* {
  box-sizing: border-box;
  max-width: 100vw;
}
@mixin texts{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
  user-select: none;
  h1 {
    font-size: 4em;
    height: 5%;
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
    transition: transform 0.2s ease;
  }
  h1:hover, p:hover{
    transform: perspective(500px) rotateX(20deg) scale(1.01);
  }
  p {
    font-size: 1.5em;
    transition: transform 0.2s ease;
  }
}
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden;
}
.Body {
  overflow-x: hidden;
  padding: 0;
  margin: 0;
  width: 100%; // Alterado de 100vw para 100%
  font-family: 'Inter', sans-serif;

  .Header {
    display: flex;
    align-items: center;
    justify-content: end;
    padding: 1.5em;
    background-color: #232323;
    width: 100%;
    height: 10vh;
    a {
      margin-right: 5%;
      color: white;
      text-decoration: none;
      font-size: 1em;
      font-family: 'Inter', sans-serif;
      transition: transform 0.5s ;
      cursor: pointer;
    }
    a::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 0;
      height: 2px;
      background-color: white;
      transition: width 0.3s ease-in-out;
    }
    a:hover::after {
      width: 100%;
    }
    a:hover{
      transform: scale(1.1);
    }
  }

  .Main {
    .Track-Home {
      height: 90vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: white;
      background-color: #232323;
      position: relative;

      .Home-Texts{
        @include texts;
      }
      
      .Home-Image {
        position: absolute;
        z-index: 1;
        left: 0;
        bottom: 0;
        width: 100%;
        img {
          width: 25%;
          max-width: 100%;
          transition: transform 0.5s;
          user-select: none;
        }
        img:hover{
          transform: scale(1.01);
        }
      }
      .Home-Ball {
        display: grid;
        place-items: center;
        position: absolute;
        width: clamp(20rem, 32vw, 40rem);
        height: clamp(18rem, 30vw, 38rem);
        bottom: -50vh; 
        right: -5vw; 
        border-radius: 50%; 
        background: #232323;
        box-shadow: 0px 8px 12px 6px rgba(0, 0, 0, 0.15), 0px 4px 4px rgba(0, 0, 0, 0.3);
        z-index: 10;
        img{
          width: 50%;
          height: auto;
          transition: transform 0.5s;
        }
        img:hover{
          transform: scale(1.01);
        }
      }
    }

    .Track-About {
      height: 100vh;
      background-color: white;
      color: #202020;
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;

      .About-Texts {
        height: 20%;
        @include texts; 
      }
    }
    .Track-Languages{
      height: 80vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      background-color: #232323;
      .Languages-Header{
        width: 100%;       
        height: 30vh;
        border-top: #202020 2px dashed;
        background-color: white;
        display: flex;
        justify-content: space-between;
         /* M3/Elevation Light/5 */
         box-shadow: 0px 8px 12px 6px rgba(0, 0, 0, 0.15), 0px 4px 4px rgba(0, 0, 0, 0.3);
        .Header-Div{
          display: flex;
          align-items: center;
          justify-content: space-around;
          width: 35%;         
        }
        img{
          width: auto;
          height: 30%;
        }
      }
      .Languages-Ball{
        top: -25vh;
        border-radius: 50%;
        position: absolute;
        width: clamp(20rem, 32vw, 40rem);
        height: clamp(18rem, 30vw, 38rem);
        /* M3/Elevation Dark/5 */
        box-shadow: 0px 8px 12px 6px rgba(0, 0, 0, 0.15), 0px 4px 4px rgba(0, 0, 0, 0.3);
        img{
          width: 100%;
          height: 100%;
          border-radius: 100%; 
          object-fit: cover;
        }
      }
      .Languages-Text{
        width: 100%;
        height: 40%;
        @include texts;
        color: white;
        p{
          width: 65%;
          text-align: center;
        }
      }
    }
    .Track-Habilities{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      background-color: #232323;
      .Habilities-Header{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 80%;
        background: #202020;
        box-shadow: inset 0px 4px 4px -1px rgba(12, 12, 13, 0.05);
        border-radius: 16px 16px 0px 0px;
        border-radius: 1em 1em 0px 0px;
        @include texts;
        color: white;
      }
      .Habilities-Main{
        color: white;
        background-color: #202020;
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        .Hability{
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 13% 0;
          .Hability-Text{
            @include texts;
            width: 65%;
            display: flex;
            justify-content: center;
            align-items: start;
            padding: 0 5%;
            margin: 0;
            h1, p {
              margin: 0;
              padding: 2% 0;
            }
            p{
              user-select: all;
              font-size: 1.2em;
              width: 85%;
            }
            p:hover{
              transform: perspective(0) rotateX(0) scale(1);
            }
          }
          img{
            transition: transform 0.5s;
          }
          img:hover{
            transform: scale(1.01);
          }
        }
      }
    }
    .Track-Projects{
      @include texts;
      display: flex;
      flex-direction: column;
      justify-content: start;
      align-items: center;
      height: 100vh;
      background-color: white;
      position: relative;
      padding: 0;
      h1{
        padding: 2% 0;
        height: auto;
        margin: 0;
        font-size: 2.5em;
      }
      img{
        position: absolute;
        bottom: -1em;
        left: 0;
        height: 60%;
        width: auto;
        transition: transform 0.5s;
      }
      img:hover{
        transform: scale(1.01);
      }
      .Project-Main{
        width: 70%;
        height: 80%;

        background: #FFFFFF;
        /* Drop Shadow/600 */
        box-shadow: 0px 16px 32px -8px rgba(12, 12, 13, 0.4);
        border-radius: 24px;

      }
    }
    .Track-Contacts {
      height: 80vh;
      background-color: #2c2c2c;
    
      .Contacts-Title {
        height: 25%;
        color: white;
        @include texts;
    
        h1 {
          margin: 0;
          padding: 0;
          height: auto;
        }
      }
    
      .Contacts-Main {
        height: 25%;
        width: 100%;
        display: flex;
        justify-content: space-around;
    
        .Contact {
          width: 20%;
          display: flex;
          align-items: center;
          justify-content: center;
          color: white;
    
          p,
          h1 {
            height: auto;
            padding: 0;
            margin: 0;
          }
    
          .Contact-Ball {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 5em; /* Base flexível (~80px se o font-size for 16px) */
            max-width: 12vw; /* Evita crescimento excessivo */
            min-width: 3.125em; /* Aproximadamente 50px */
            aspect-ratio: 1/1; /* Mantém o formato circular */
            border-radius: 50%;
            background: #ffffff;
            margin: 0 5%;
            box-shadow: 0 0 1em rgba(255, 255, 255, 0.3); /* Suaviza o efeito */
            flex-shrink: 0; /* Impede distorções no flexbox */
          
            img {
              width: 50%; /* Mantém o tamanho proporcional */
              height: auto;
            }
          }          
        }
      }
    
      .Contacts-Text {
        color: white;
        @include texts;
        justify-content: start;
        align-items: start;
        padding: 0 5%;
        height: 50%;
    
        h1 {
          font-size: 2.5em;
        }
    
        p {
          padding: 2% 0;
          font-size: 1em;
          width: 60%;
        }
    
        img {
          height: 40%;
          right: 0;
          position: absolute;
          transition: transform 0.5s;
        }
    
        img:hover {
          transform: scale(1.01);
        }
      }
    }    
  }
  .Footer {
    display: flex;
    width: 100%;
    padding: 0;
    margin: 0;
    background-color: #202020;
    height: 20vh;
    color: white;
    @include texts;

    p {
        font-size: 1em;
    }

    .Footer-Links {
        position: absolute;
        display: flex;
        justify-content: space-around;
        align-items: center;  
        right: 0;
        width: 13%;
        margin-right: 5%;
        cursor: pointer;

        img {
            transition: transform 0.5s;
            height: 2.5em;  
            width: auto;  
        }

        img:hover {
            transform: scale(1.1);
        }
    }
}
}
/* Desktops menores e laptops (até 1280px) */
@media (max-width: 1280px) {
  @mixin texts {
    h1 {
      font-size: 3em !important;
    }
    p {
      font-size: 1em !important;
    }
  }
  
  .About-Texts,
  .Track-Habilities,
  .Home-Texts,
  .Languages-Text {
    @include texts;
  }

  .Home-Ball {
    bottom: -40vh !important;
  }

  .Languages-Header img {
    max-height: 20%;
  }

  .Languages-Ball {
    top: -10vh !important;
    max-width: clamp(15rem, 27vw, 35rem);
    max-height: clamp(13rem, 25vw, 33rem);
  }

  .Languages-Text {
    color: white;
    p {
      min-width: 90%;
      text-align: center;
    }
  }
}

/* Tablets e dispositivos médios (até 1024px) */
@media (max-width: 1024px) {
  @mixin texts {
    h1 {
      font-size: 2em !important;
    }
    p {
      font-size: 0.8em !important;
    }
  }

  .About-Texts,
  .Track-Habilities,
  .Home-Texts,
  .Contacts-Text {
    @include texts;
  }

  .Hability {
    padding: 4% !important;
    img {
      width: 70%;
    }
  }

  .Home-Ball {
    bottom: -35vh !important;
  }

  .Languages-Header img {
    max-height: 20%;
  }

  .Languages-Ball {
    top: -10vh !important;
    max-width: clamp(15rem, 27vw, 35rem);
    max-height: clamp(13rem, 25vw, 33rem);
  }

  .Languages-Text {
    color: white;
    p {
      min-width: 90%;
      text-align: center;
    }
  }

  .Contact-Ball {
    width: 3em !important;
  }

  .Contacts-Title h1,
  .Track-Projects h1 {
    font-size: 2em !important;
  }

  .Contact {
    h1 {
      font-size: 1em;
    }
    p {
      font-size: 0.6em;
    }
  }

  .Footer-Links {
    width: 13%;
    img {
      height: 1.5em !important;
    }
  }
}

/* Celulares médios e grandes (até 768px) */
@media (max-width: 768px) {
  .Home-Ball {
    bottom: -25vh !important;
    max-width: clamp(15rem, 27vw, 35rem);
    max-height: clamp(13rem, 25vw, 33rem);
  }

  .Languages-Header {
    height: 20vh !important;
    .Header-Div {
      width: 25% !important;
      height: 100%;
    }
    img {
      width: auto;
      max-height: 15% !important;
    }
  }

  .Track-About {
    height: 70vh !important;
  }

  .Track-Languages {
    height: 50vh !important;
  }

  .Hability {
    flex-direction: column;
    text-align: center;
    padding: 20% 0 !important;

    .Hability-Text,
    h1,
    p {
      width: 100% !important;
      text-align: center;
    }
  }

  .Track-Projects {
    @include texts;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    height: 100vh;
    background-color: white;
    position: relative;
    padding: 0;

    h1 {
      padding: 2% 0;
      height: auto;
      margin: 0;
      font-size: 2.5em;
    }

    img {
      position: absolute;
      bottom: -1em;
      left: 0;
      height: 60%;
      width: auto;
      transition: transform 0.5s;
    }

    img:hover {
      transform: scale(1.01);
    }

    .Project-Main {
      width: 70%;
      height: 80%;
      background: #ffffff;
      box-shadow: 0px 16px 32px -8px rgba(12, 12, 13, 0.4);
      border-radius: 24px;
    }
  }

  .Track-Contacts {
    padding-top: 5vh;
    height: 80vh !important;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;

    .Contacts-Title {
      height: 10% !important;
      margin-bottom: 1em;
    }

    .Contacts-Main {
      height: 50% !important;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;

      .Contact {
        width: 60% !important;

        .Contact-Text {
          width: 50%;
          text-align: start;
        }
      }
    }

    .Contacts-Text {
      flex-direction: column;
      justify-content: space-between !important;
      align-items: center !important;
      height: auto !important;

      h1 {
        font-size: 1em !important;
        width: 100% !important;
        height: 40% !important;
        text-align: center;
      }

      p {
        padding: 0 !important;
        width: 100% !important;
        height: 20% !important;
        font-size: 0.5em !important;
      }

      img {
        height: 30vh !important;
        position: relative !important;
      }
    }
  }

  .Footer {
    flex-direction: column;
    height: 20vh !important;
    .Footer-Links{
      position: relative !important;
      width: 60% !important;
      margin-right: 0% !important;
    }
    p {
      font-size: 0.8em !important;
    }
  }
}


/* Telas pequenas (celulares pequenos, até 480px) */
@media (max-width: 480px) {
